var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"ha-argumentaire"},[_c('h2',[_vm._v("Argumentaire")]),_c('div',{staticClass:"ha-content"},[_c('p',[_vm._v(" L’évolution des diverses théories de l’apprentissage qui ont traversé le siècle dernier dénote de la préoccupation majeure qu’est celle de l’enseignement : améliorer son efficacité en répondant aux attentes de la société, en d’autres termes, réfléchir à des modèles pédagogiques innovants permettant de fluidifier le passage du monde académique au monde professionnel en intégrant le souci de l’employabilité dans le système éducatif. Dès lors, aujourd’hui, plus que jamais, l’épineuse question des pratiques pédagogiques se pose avec force. Le contexte socioéconomique en perpétuelle mutation - en témoigne l’apparition sur le marché du travail de nouveaux métiers jusqu’à maintenant inédits - remet au centre de la réflexion pédagogique la nécessité urgente de repenser ses pratiques. Seule issue possible pour s’adapter à cet environnement qui nous échappe est d’envisager autrement l’agir professoral en instaurant de nouvelles méthodes plus à même de répondre à une société de plus en plus exigeante. Les méthodes traditionnelles se doivent d’être revues, corrigées, réorientées en veillant à ce que l’apprenant soit au centre de l’apprentissage, acteur de sa transformation. La modification profonde du contexte où évolue l’apprenant nous invite légitimement à faire preuve de novation, autrement dit, à sortir des sentiers battus. Aussi, l’université se doit donc de mettre à jour ses actions afin d’accompagner au mieux ses étudiants dans l’acquisition de compétences leur permettant une meilleure insertion professionnelle tout en les dotant d’une forte capacité d’adaptation. ")]),_c('p',[_vm._v(" Il est donc grand temps de trouver des solutions concrètes et innovantes à ce problème socio-économique et ainsi offrir aux jeunes un enseignement à même de leur garantir un travail à la hauteur de leurs espérances et de leurs compétences. Dans cette perspective, la recherche de l’efficience des dispositifs de formation universitaire s’avère importante et, à cet effet, l’approche par compétences, préconisée par de nombreux chercheurs en éducation et formation, constitue pour Jacques Tardif (2020) « un cadre de référence » servant à « déterminer ce qui doit être appris (…), évaluer la qualité de l’enseignement et des prestations de ses acteurs, (…) ajuster les programmes et (…) en construire de nouveau, davantage en phase avec les besoins de la société en matière de construction identitaire , de citoyenneté démocratique et de professionnalisation ». ")]),_c('p',[_vm._v(" C’est dans ce cadre que se situe le thème de ce colloque qui a pour dessein de retracer les grandes tendances observables en la matière. L’idée est d’accéder à un certain nombre de dispositifs pédagogiques considérés par leurs praticiens comme innovants et représentatifs des tendances contemporaines au sein de l’université. ")]),_c('p',[_vm._v(" Ce colloque s’attend à des communications qui mettront en exergue les différentes pratiques et les dispositifs pédagogiques mobilisés par les enseignants chercheurs et les formateurs pédagogues à l’université. A cet égard, un débat, proposant des pistes pour une ingénierie pédagogique, s’impose dans la perspective d’accompagner les étudiants tout au long de leur formation pour leur permettre l’acquisition de compétences mesurables et compatibles avec les attentes du marché de l’emploi. ")]),_c('p',[_vm._v(" Ainsi serait-il judicieux d’analyser les différentes compétences dont l’étudiant marocain a urgemment besoin dans un contexte économique porté sur la sélection des profils au niveau du recrutement. Nous estimons que la capacité à penser de façon critique, à résoudre des problèmes, à analyser, à comprendre l’autre, à évaluer, à s’autoévaluer, à s’adapter à des situations changeantes, à prendre des décisions, à coordonner, à négocier, à créer, à innover, à communiquer, à agir sont autant de compétences que l’étudiant doit acquérir avant d’obtenir son diplôme. ")]),_c('p',[_vm._v(" Cette première édition se veut ainsi l’outil fédérateur des différentes pratiques formatives innovantes à l’université. Pour cela, le colloque portera sur les axes suivants : ")]),_c('div',[_c('Strong',[_vm._v("1er Axe : Innovation pédagogique à l’université")]),_vm._m(0)],1),_vm._m(1),_vm._m(2),_vm._m(3)]),_c('h2',[_vm._v("Références bibliographiques")]),_vm._m(4)])}
